.modalWrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0000003b;
    display: flex;
    justify-content: center;
    align-items: center;
}
.modal {
    max-width: 70%;
    height: auto;
    background: #fff;
    border-radius: 5px;
}